










































import { Component, Vue } from "vue-property-decorator";
import cryptoCurrencyService from "./crypto-currency.services";
import CryptoCoins from "./crypto-currency-model";
import store from "../../src/store";
@Component
export default class CryptoDisplay extends Vue {
  items: Array<any> = [];
  rows = 0;
  currentPage = 1;
  perPage = 10;
  filterColumn: any = [];
  options = [10, 25, 50, 100];

  created() {
    const result: any = cryptoCurrencyService.getCyrptoCoins();
    this.updateRow(result);
    this.filterColumn = [
      "index",
      {
        key: "Icon"
      },
      {
        key: "Name",
        sortable: true
      },
      {
        key: "Symbol",
        sortable: true
      },
      {
        key: "Price",
        sortable: true
      },
      {
        key: "Change in Price",
        sortable: true
      }
    ];
  }

  get perPageRow() {
    return this.perPage;
  }

  updateRow(coinsData: any) {
    // store.commit("setCoinData", coinsData);
    this.items = store.getters.getCoinsData;
    this.rows = this.items.length;
  }
}
